.header-color{
  background: #3A1C71;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #FFAF7B, #D76D77, #3A1C71);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #FFAF7B, #D76D77, #3A1C71); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.landing-grid{
  background: #3A1C71;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #FFAF7B, #D76D77, #3A1C71);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #FFAF7B, #D76D77, #3A1C71); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
text-align: center;
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;

}
.avatar-img{
  height: 250px;
  padding-top: 5em;
}
.banner-text{
  background-color: black;
  opacity: .8;
  width:75%;
  margin:auto;
  border-radius: 10px;
}
.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
  
}
.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}
.banner-text p{
  color: white;
  padding: 1em;
  font-size: 20px;
  
}
.social-links{
display:flex;
justify-content: space-evenly;
width: 50%;
margin: auto;

}
.social-links i{
  color: white;
  font-size: 5em;
}
.projects-grid{
  display: flex;
  justify-content: space-evenly;
}

/* COntact*/

.contact-body {
  margin: auto;
  
  position: fixed;
  height: 100%;
  width:100%;
  justify-content: center;
  padding-top: 2em;

}
 
.contact-grid{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  
  margin-bottom: 20px;
}

.contact-grid h2{
  font-family: 'Anton', sans-serif;
}

.contact-grid p{
  font-family: 'Oxygen', sans-serif;
}
.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}
.contact-list i{
  font-size: 66px;
  padding-right: 5rem;
}
 /* Resume page css*/
 .resume-right-col{
   background: #27221F;
   color:white;
   padding: 2em;
 }
 .aboutme-grid{
  background: #FFAFBD;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #ffc3a0, #FFAFBD);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ffc3a0, #FFAFBD); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
 }
 .aboutme-text h1{
   padding-top: 2rem;
  font-size: 66px;
  font-weight: bold;
  color: rgb(255, 255, 255);
  
}
.aboutme-text p{
  
  padding: 1em;
  font-size: 20px;
  color: rgb(0, 0, 0);
}






